// this 02offer.scss imported in _root.scss
.offer{
  background-image: url(../images/bg/offer.png);
  background-position-x: center;
  background-repeat: no-repeat;
  background-size: cover;
  padding-top: 2rem;
  padding-bottom: 12.8rem;
  &__text{
    margin-top: 6rem;
    max-width: 700px;
  }
  // swiper in offer
  &__slider{
    overflow: hidden;
    padding-top: 95px;
  }
  .swiper{
    overflow: initial;
    &-slide{
      height: 521px;
      padding: 213px 0 40px 42px;
      background-repeat: no-repeat;
      background-size: cover;
      &__title{
        font-family: $font-corm;
        font-weight: 700;
        font-size: 3rem;
        margin: 0;
        max-width: 270px;
        position: absolute;
        bottom: 36px;
      }
      &__text{
        display: none;
        font-family: $font-jost;
        font-size: 1.6rem;
        font-weight: 400;
        opacity: 0.70;
        max-width: 270px;
        margin-top: 25px;
        line-height: 1.5;
      }
      &__link{
        display: none;
        margin-top: 26px;
        margin-left: -4px;
        text-decoration: none;
        color: $green;
        font-family: $font-jost;
        font-size: 1.6rem;
        font-weight: 500;
        opacity: 0.8;
        padding-left: 52px;
        position: relative;
        letter-spacing: .13rem;
        text-transform: uppercase;
        &:hover{
          opacity: 1;
          &::before{
            opacity: 1;
          }
        }
        &::before{
          content: '';
          position: absolute;
          left: 0; top: -6px;
          width: 31px;
          height: 31px;
          background-image: url(../images/plus.svg);
          background-size: cover;
          background-repeat: no-repeat;
          opacity: .8;
        }
      }
      //???
      &:hover{
        .swiper-slide__title{
          position: static;
        }
        .swiper-slide__text{
          display: block;
        }     
        .swiper-slide__link{
          display: inline-block;
        }
      }
      &::after{
        content: '';
        position: absolute;
        top: 0;
        left: 0;
        width: 100%;
        height: 100%;
        opacity: 1;
        box-sizing: border-box;
        border: 1px solid rgba($green, 0.3) ;
        background-repeat: no-repeat;
        background-size: contain;
        border-radius: 20px;
        z-index: -1;
      }
      &:hover::after{
        opacity: 0.5;
        border: 3px solid rgba($green, 0.3) ;
      }
      &_milk::after{
        background-image: url(../images/milk.png);
      }
      &_pasta::after{
        background-image: url(../images/pasta.png);
      }
      &_meat::after{
        background-image: url(../images/meat.png);
      }
    }
  }
}
.swiper{
  &-slide{
    box-sizing: border-box;
    position: relative;
    border-radius: 20px;
  }
  &-pagination{
    display: none;
  }
  &-button-next, &-button-prev{
    top:-60px;
    // right: 0px;
    &:hover{
      &::after{
        color: rgba($green, 1);
      }
    }
    &::after{
      color: rgba($green, 0.4);
      font-size: 20px;
    }
  }
  &-button-next{
    right: -8px;
  }  
  &-button-prev{
    left: initial;
    right: 72px;
  }
}